:root {
  --summerpink: rgb(255, 142, 208); /*border*/
  --summeryellow: #ffff00; /*btnColor*/
  --summergreen: #00ffb3; /*underlines*/
  --summerblue: #0ac6ff; /*header*/

  --christmasgreen: #006646; /*header*/
  --christmasred: red; /*border*/
  --christmassilver: #f1f1f1; /*text*/
  --christmasblue: #9ce3ff; /*underlines*/

  --autumnred: red; /*underlines*/
  --autumnyellow: rgb(250, 225, 0); /*btnColor*/
  --autumnorange: rgb(255, 145, 0); /*header*/
  --autumngreen: #006646; /*border*/

  /* headers */
  --headerColor: var(--summerblue);
  --textColor: var(black);
  --borderColor: var(--summerpink);
  --underlines: var(--summergreen);
  --btnColor: var(--summeryellow);
}

* {
  margin: 0;
  padding: 0;
  text-align: center;
}

header {
  background-color: var(--headerColor);
  color: var(--black);
  border-bottom: 5px solid var(--borderColor);
  display: grid;
  grid-template-columns: 1fr 3fr 1fr;
}

nav {
  grid-column: 1 / 4;
  padding: 0.7em;
  background-image: linear-gradient(90deg, white, var(--btnColor), white);
}

nav a {
  padding: 2em;
}

.imgDiv {
  width: 100%;
  height: auto;
  margin: 1em;
}
.imgDiv img{
  border-radius: 10px;
}
header .imgDiv img {
  border: 2px solid var(--borderColor);
  border-radius: 50%;
  background-color: #f2f2f2;
}

.top {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
}

h1 {
  margin: 1em;
}

.top h2 {
  grid-column: 2 / 3;
}

.importBtn {
  grid-column: 3 / 4;
  /* height: 2.5em;
  width: 6.5em;
  margin-top: 1.5em; */
  width: 7em;
  height: 2.5em;
  margin: 1em auto;
  color: black;
  background-image: linear-gradient(
    0deg,
    var(--btnColor),
    var(--underlines),
    white
  );
  border-image: linear-gradient(white, var(--underlines), var(--btnColor)) 10;
  border-width: 3px;
  border-style: solid;
}



/* .account {
  grid-column: 1 / 3;
  margin-bottom: 0.5em;
  color: var(--textColor);
  font-size: 1.1em;
} */

main {
  width: 20em;
  margin: 0.5em auto 3em;
}

h2 {
  margin: 1em;
}

.wideForm {
  width: 20em;
  background-color: var(--textColor);
  border: 5px double var(--borderColor);
  display: grid;
  margin-bottom: 1em;
}

.wrap {
  width: 20px;
  height: 1.5em;
  margin: 2%;
  margin-bottom: 1.5em;
  display: grid;
  text-align: left;
}

.wrap input {
  width: 22em;
  padding: 0.3em;
}

.options {
  display: grid;
  margin-bottom: 1em;
}

.catLabel {
  width: 15em;
  margin: 1em auto;
  border-bottom: 3px dashed var(--underlines);
}

/* checkboxes */
.options label {
  margin-left: 1em;
  text-align: left;
  width: 8em;
}

.options label input {
  scale: 1.5;
  margin: 0.8em;
}

/* submit btn */
.submit,
.createItem {
  width: 7em;
  height: 2.5em;
  margin: 1em auto;
  color: black;
  background-image: linear-gradient(
    0deg,
    var(--btnColor),
    var(--underlines),
    white
  );
  border-image: linear-gradient(white, var(--underlines), var(--btnColor)) 10;
  border-width: 3px;
  border-style: solid;
}

/* list collection */
.lists {
  background-color: var(--textColor);
  border: 3px solid var(--borderColor);
  list-style-type: none;
}

ul a {
  color: black;
  text-decoration: none;
}

.lists li {
  border: 1.5px solid var(--underlines);
  border-radius: 10px;
  margin: 2%;
  display: grid;
  grid-template-columns: 100px 2fr 1fr;
  /* width: 10em; */
  /* display: flex; */
  /* flex-direction: row; */
  /* align-items: center; */
  /* justify-content:flex-start; */
}

.lists h3 {
  margin: auto;
}

.itemList a li:not(.singleCol li) {
  display: grid;
  grid-template-columns: 1fr 1fr;
  position: relative;
  /* text-align: left; */
}

.itemList h3 {
  grid-column: 1 / 3;
  margin: 0.5em;
}

.itemList h4 {
  margin: 0.5em auto;
  width: 45%;
  border-bottom: 2px dashed var(--headerColor);
}

.itemList ul li {
  border: none;
}

.singleCol li {
  display: block;
}

.itemList button {
  position: absolute;
  right: 0.5em;
  top: 0.5em;
}

.accountBtn {
  color: black;
}

.delete {
  width: 1.8em;
  height: 1.8em;
  background-color: rgb(245, 9, 9);
  border-style: none;
  box-shadow: 0em 0em 0.5em red;
  border-radius: 5px;
  margin: auto;
}

.listTop {
  display: grid;
  grid-template-columns: auto 1fr auto;
}

.listTop .imgDiv {
  margin: auto;
}

.addList {
  border: 5px double var(--borderColor);
  padding-top: 0.5em;
}

.addList .wrap input {
  width: 21em;
}

.hiddenContainer {
  margin: 0;
  padding: 1em;
  position: fixed;
  top: 8em; 
  background-color: white;
  border-radius: 10px;
  list-style-type: none;
  border: 2px solid var(--summerpink);
}

.mask {
  position: fixed;
  left: 0;
  top: 0;
  margin: 0;
  padding: 0;
  width: 100vw;
  height: 100vh;
  background-color: rgba(0, 0, 0, 0.445);
}

.home .hiddenContainer ul {
  display: grid;
  list-style-type: none;
}

.listBtn {
  width: 15em;
  height: 4em;
  margin: 0.5em auto;
  border-radius: 10px;
  background-color: var(--summeryellow);
  border: 2px outset var(--summerblue)
}

.requestPanel {
  padding: 2em;
}

.requestBtns button {
  margin: 1em;
  padding: 0.8em 2em;
  border-radius: 10px;
}

.yes {
  background-color: var(--summergreen);
  border: 2px outset var(--summergreen)
}

.no {
  background-color: var(--autumnred);
  border: 2px outset var(--autumnred)
}
/* 
.hiddenPanel {
  display: none;
} */

footer {
  background-color: var(--headerColor);
  color: var(--textColor);
  padding: 1em;
}

.failed{
  border:1px solid red;
  border-radius: .25em;
}

#listImageSettings:not(.hide){
  margin: 1em 2%;
  padding:0.2em;
  display:grid;
  grid-template-columns: 1fr 1fr;
}
#listImageSettings label{
  grid-column:1/2;
}
#listImageSettings > canvas{
  grid-column:2/3;
  grid-row:1/4;
  border:1px solid black;
  border-radius: 15px;
  margin:auto;
  width:80%;
}
.hide{
  display:none;
}

.noSelect {
  user-select: none;
}

